<template>
  <div>
    <!-- <router-link :to="active" v-if="active === '/chat-ui'">
      <button
        type="button"
        @click="active = '/gym'"
        class="nes-btn is-primary"
      >
        {{ nextLink }}
      </button>
    </router-link> -->

    <button type="button" @click="toggle" class="nes-btn is-primary">
      &lt; {{ active }}
    </button>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  mounted() {},
  data() {
    return {
      active: "/chat-ui",
    };
  },
  methods: {
    toggle() {
      if (this.active === "/chat-ui") {
        this.$router.push({ path: "/chat-ui" });
        this.active = "/gym";
      } else {
        this.$router.push({ path: "/gym", query: { skip_demo: "yes" } });
        this.active = "/chat-ui";
      }
    },
  },
  watch: {
    $route: {
      immediate: true,
      handler: function() {
        if (this.$route.fullPath === "/chat-ui") {
          this.active = "gym";
        } else {
          this.active = "/chat-ui";
        }
      },
    },
  },
};
</script>

<style></style>
